import React, { useState } from "react";

import "./BoardSummaryPage.scss";
import { getBoardEstimates, SummaryEstimate } from "../api/estimateApi";
import { formatMinutes } from "../utils/timeParsing";
import { exportEstimatesForMail, exportEstimatesToTsv, exportToFile } from "../utils/export";
import { useProvidedTrello } from "../hooks/useProvidedTrello";
import { useWorkSessions } from "../hooks/useWorkSessions";
import { useCurrentWork } from "../hooks/useCurrentWork";
import { useElapsedMinutes } from "../hooks/useElapsed";
import { WorkButton } from "../components/WorkButton";
import { WorkSessions } from "../components/WorkSessions";
import { EndWorkSessionButton } from "../components/EndWorkSessionButton";
import { DeployInfo } from "../components/DeployInfo";
import { useTrelloRender } from "../hooks/useTrelloRender";
import { If } from "../jk/types/jsxControlStatements";
import { formatWorkDelta } from "../utils/workDelta";
import { useMarginMultiplier } from "../hooks/useMarginMultiplier";
import { MarginInput } from "../components/MarginInput";

export default function BoardSummaryPage() {
    const t = useProvidedTrello();
    const [summary, setSummary] = useState<SummaryEstimate>();
    const margin = useMarginMultiplier(t);
    const workSessions = useWorkSessions(t);
    const currentWork = useCurrentWork();
    const elapsedWorkMinutes = useElapsedMinutes(currentWork.value?.startDate);

    useTrelloRender(t, async () => {
        const fetched = await getBoardEstimates(t);
        setSummary(fetched);
    });

    if (summary === undefined)
        return (<p>Loading... </p>);

    const currentSummary: SummaryEstimate = {
        estimateMinutes: summary.estimateMinutes,
        updatedEstimateMinutes: summary.updatedEstimateMinutes,
        workMinutes: summary.workMinutes + elapsedWorkMinutes,
        remainingMinutes: summary.remainingMinutes - elapsedWorkMinutes,
        deltaMinutes: summary.deltaMinutes,
    };

    const marginMinutes = margin.value > 0
        ? margin.value * summary.estimateMinutes
        : 0
    ;

    const hasUpdatedEstimate = currentSummary.updatedEstimateMinutes > 0
        && currentSummary.updatedEstimateMinutes !== currentSummary.estimateMinutes
    ;

    return (
        <div className='summary'>
            <div className="section">
                <div>
                    {hasUpdatedEstimate && "Original "}
                    Estimate: {formatMinutes(currentSummary.estimateMinutes)}
                    <If condition={marginMinutes > 0}>
                        {" "} (+ margin: {formatMinutes(marginMinutes)})
                    </If>
                </div>
                <If condition={hasUpdatedEstimate}>
                    <div>
                    Updated Estimate: {formatMinutes(currentSummary.updatedEstimateMinutes)}
                    </div>
                </If>
                <div>
                    Work: {formatMinutes(currentSummary.workMinutes)}
                </div>
                <div>
                    Remaining: {formatMinutes(currentSummary.remainingMinutes)}
                </div>
                <div>
                    {formatWorkDelta({ delta: summary.deltaMinutes })}
                </div>
            </div>

            <MarginInput className="section" />

            <div className="section work">
                <WorkSessions />
                <div className="buttons">
                    <EndWorkSessionButton />
                    <WorkButton onEstimateChanged={() => workSessions.refetch()} />
                </div>
            </div>
            {/* <button onClick={async () => {
                const sessions = await getWorkSessions(t);
                sessions.pop();
                sessions[sessions.length - 1].endDate = undefined;
                await setWorkSessions(t, sessions);
            }}>
                delete last session
            </button> */}
            <div className='export-section'>
                <div className='export-inputs'>
                    <button
                        className='mod-primary'
                        onClick={async () => exportToFile(
                            await exportEstimatesToTsv(t, margin.value),
                            "estimate.tsv",
                        )}
                    >
                        Export estimates
                    </button>
                    <button
                        className='mod-primary'
                        onClick={async () => exportToFile(
                            await exportEstimatesForMail(t, margin.value),
                            "estimate.txt",
                        )}
                    >
                        Export mail
                    </button>
                </div>
            </div>
            <DeployInfo className="deploy-info" />
        </div>
    );
}
